import { fetchGraphQl } from '@/b2b-purchase-order/api/fetch-graphql';
import { handleNetworkError } from '@/b2b-purchase-order/lib/network-error';
import { handleFetchError } from '@/b2b-purchase-order/lib/fetch-error';
import { GET_PURCHASE_ORDER } from '@/b2b-purchase-order/api/getPurchaseOrder/graphql/getPurchaseOrder.graphql';
import { transformPurchaseOrder } from '@/b2b-purchase-order/data/transforms/transform-purchase-order';
import { PurchaseOrderModel } from '@/b2b-purchase-order/data/models';

export const getPurchaseOrder = async (
  uid: string
): Promise<{
  purchaseOrder: PurchaseOrderModel;
}> => {
  if (!uid || uid.trim() === '') {
    throw new Error('Purchase Order UID is required');
  }

  return fetchGraphQl(GET_PURCHASE_ORDER, {
    variables: { uid },
  })
    .then((response: any) => {
      if (response.errors?.length) {
        handleFetchError(response.errors);
      }

      const result = response.data?.customer?.purchase_order;
      if (!result) {
        throw new Error('Failed to get purchase order');
      }

      return {
        purchaseOrder: transformPurchaseOrder(result),
      };
    })
    .catch(handleNetworkError);
};
