import { Initializer } from '@adobe-commerce/elsie/lib';
import { Lang } from '@adobe-commerce/elsie/i18n';
import { getPurchaseOrder } from '../getPurchaseOrder';
import { events } from '@adobe-commerce/event-bus';

type ConfigProps = {
  langDefinitions?: Lang;
  poRef?: string;
};

const fetchAndEmitPurchaseOrderData = async (poRef: string) => {
  const result = await getPurchaseOrder(poRef);

  if (result?.purchaseOrder?.quote) {
    events.emit('order/data', {
      ...result.purchaseOrder.quote,
    });
    events.emit('purchase-order/data', {
      ...result.purchaseOrder,
    });
  } else {
    console.error('Failed to fetch purchase order data.');
  }
};

export const initialize = new Initializer<ConfigProps>({
  init: async (config) => {
    const defaultConfig = {};

    initialize.config.setConfig({ ...defaultConfig, ...config });

    const isValidPoRef =
      typeof config?.poRef === 'string' && config.poRef.trim() !== '';

    if (isValidPoRef) {
      await fetchAndEmitPurchaseOrderData(config!.poRef!);
    }
  },

  listeners: () => [
    events.on('purchase-order/refresh', async () => {
      const config = initialize.config.getConfig();
      const isValidPoRef =
        typeof config?.poRef === 'string' && config.poRef.trim() !== '';

      if (isValidPoRef) {
        await fetchAndEmitPurchaseOrderData(config!.poRef!);
      }
    }),
  ],
});

export const config = initialize.config;
