import { fetchGraphQl } from '@/b2b-purchase-order/api/fetch-graphql';
import { handleNetworkError } from '@/b2b-purchase-order/lib/network-error';
import { handleFetchError } from '@/b2b-purchase-order/lib/fetch-error';
import { PLACE_PURCHASE_ORDER } from '@/b2b-purchase-order/api/placePurchaseOrder/graphql/placePurchaseOrder.graphql';
import { transformPurchaseOrder } from '@/b2b-purchase-order/data/transforms/transform-purchase-order';
import { PurchaseOrderModel } from '@/b2b-purchase-order/data/models';
import { events } from '@adobe-commerce/event-bus';

export const placePurchaseOrder = async (
  cartId: string
): Promise<{ purchaseOrder: PurchaseOrderModel }> => {
  if (!cartId || cartId.trim() === '') {
    throw new Error('Cart ID is required');
  }

  const input = {
    cart_id: cartId,
  };

  return fetchGraphQl(PLACE_PURCHASE_ORDER, {
    variables: { input },
  })
    .then((response: any) => {
      if (response.errors?.length) {
        handleFetchError(response.errors);
      }

      const rawPurchaseOrder =
        response.data?.placePurchaseOrder?.purchase_order;

      const purchaseOrder = transformPurchaseOrder(rawPurchaseOrder);

      events.emit('purchase-order/placed', purchaseOrder);

      return {
        purchaseOrder,
      };
    })
    .catch(handleNetworkError);
};
