import { CartModel } from '@/b2b-purchase-order/data/models';

export const transformCart = (data: any): CartModel => {
  const cartItems = data?.cart?.itemsV2?.items ?? [];

  return {
    cart: {
      id: data?.cart?.id ?? '',
      items: cartItems.map((item: any) => ({
        uid: item?.uid ?? '',
        quantity: item?.quantity ?? 0,
        product: {
          uid: item?.product?.uid ?? '',
          name: item?.product?.name ?? '',
          sku: item?.product?.sku ?? '',
        },
      })),
      pagination: {
        currentPage: data?.cart?.itemsV2?.page_info?.current_page ?? 1,
        pageSize: data?.cart?.itemsV2?.page_info?.page_size ?? 20,
        totalPages: data?.cart?.itemsV2?.page_info?.total_pages ?? 0,
        totalCount: data?.cart?.itemsV2?.total_count ?? 0,
      },
    },
    userErrors:
      data?.user_errors?.map((error: any) => ({
        message: error?.message ?? '',
      })) ?? [],
  };
};
