import { fetchGraphQl } from '@/b2b-purchase-order/api/fetch-graphql';
import { handleNetworkError } from '@/b2b-purchase-order/lib/network-error';
import { handleFetchError } from '@/b2b-purchase-order/lib/fetch-error';
import { transformCart } from '@/b2b-purchase-order/data/transforms';
import { ADD_PURCHASE_ORDER_ITEMS_TO_CART } from '@/b2b-purchase-order/api/addPurchaseOrderItemsToCart/graphql/addPurchaseOrderItemsToCart.graphql';
import { CartModel } from '@/b2b-purchase-order/data/models';

export const addPurchaseOrderItemsToCart = async (
  purchaseOrderUid: string,
  cartId: string,
  replaceExistingCartItems: boolean = false
): Promise<CartModel> => {
  if (!purchaseOrderUid) {
    throw new Error('Purchase Order UID is required');
  }

  if (!cartId) {
    throw new Error('Cart ID is required');
  }

  const variables = {
    purchaseOrderUid,
    cartId,
    replaceExistingCartItems,
  };

  return fetchGraphQl(ADD_PURCHASE_ORDER_ITEMS_TO_CART, {
    variables,
  })
    .then((response: any) => {
      if (response.errors?.length) {
        handleFetchError(response.errors);
      }

      const cartData = response.data?.addPurchaseOrderItemsToCart;
      if (!cartData?.cart) {
        throw new Error('Failed to add purchase order items to cart');
      }

      return transformCart(cartData);
    })
    .catch(handleNetworkError);
};
