import { PURCHASE_ORDERS_FRAGMENT } from '../../fragments';

export const GET_PURCHASE_ORDER = /* GraphQL */ `
  query GET_PURCHASE_ORDER($uid: ID!) {
    customer {
      purchase_order(uid: $uid) {
        ...PURCHASE_ORDERS_FRAGMENT
      }
    }
  }
  ${PURCHASE_ORDERS_FRAGMENT}
`;
