import { fetchGraphQl } from '@/b2b-purchase-order/api/fetch-graphql';
import { handleNetworkError } from '@/b2b-purchase-order/lib/network-error';
import { handleFetchError } from '@/b2b-purchase-order/lib/fetch-error';
import { VALIDATE_PURCHASE_ORDERS } from '@/b2b-purchase-order/api/validatePurchaseOrders/graphql/validatePurchaseOrders.graphql';
import { PurchaseOrderModel } from '@/b2b-purchase-order/data/models';
import { transformPurchaseOrder } from '@/b2b-purchase-order/data/transforms/transform-purchase-order';

export const validatePurchaseOrders = async (
  uids: string | string[]
): Promise<{
  errors: { message: string; type: string }[];
  purchaseOrders: PurchaseOrderModel[];
}> => {
  const uidArray = Array.isArray(uids) ? uids : [uids];

  if (!uidArray || uidArray.length === 0) {
    throw new Error('Purchase Order UID(s) are required');
  }

  if (uidArray.some((uid) => !uid || uid.trim() === '')) {
    throw new Error('All Purchase Order UIDs must be valid');
  }

  const input = {
    purchase_order_uids: uidArray,
  };

  return fetchGraphQl(VALIDATE_PURCHASE_ORDERS, {
    variables: { input },
  })
    .then((response: any) => {
      if (response.errors?.length) {
        handleFetchError(response.errors);
      }

      const data = response.data?.validatePurchaseOrders;

      return {
        errors: (data?.errors ?? []).map((error: any) => ({
          message: error?.message ?? '',
          type: error?.type ?? '',
        })),
        purchaseOrders: (data?.purchase_orders || []).map(
          transformPurchaseOrder
        ),
      };
    })
    .catch(handleNetworkError);
};
